.icon__reuse-filter--calend {
    font-size: 1rem;
    color: rgba(0,0,0, .6);
    cursor: pointer;
}

.icon__reuse-filter--cnt__angle {
    margin-right: 5px;
}

.icon__reuse-filter--categ {
    color: #182427;
    margin-right: 10px;
}

.icon__reuse-filter--apply {
    color: #fff;
    margin-right: 5px;
}

.icon__reuse-filter--cancel {
    color: #ff1600;
    margin-right: 5px;
}

.icon__reuse-filter--close {
    color: #ff1600;
}

.reuse-filter__opt {
    position: absolute;
    border: 1px solid #dcdbdc;
    background-color: #fff;
    box-shadow: 0px 0px 2px 2px rgba(0,0,0, .45);
    padding: 10px;
    width: 95%;
    left: 50%;
    transform: translateX(-50%);
    top: 100px;
    font-size: .875rem;
    z-index: 2;
}

.reuse-filter__opt--srch {
    position: relative;
    background-color: #e9ebf2;
    border: 1px solid #dcdbdc;
    border-radius: 3px;
    width: 100%;
}

.reuse-filter__opt--srch > div {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #f2f2f2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    width: 60px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    border-right: 1px solid #dcdbdc;
}

.reuse-filter__opt--srch__input {
    border: none;
    outline: none;
    padding: 8px 10px;
    border-radius: 3px;
    width: 100%;
}

@media (min-width: 530px) {

    .reuse-filter__opt--srch__input {
        padding: 12px 10px;
    }
}

.reuse-filter__opt--srch__no-total {
    margin-bottom: 30px;
}

.reuse-filter__opt--fnd {
    position: relative;
    margin: 10px 0;
    padding: 30px 0 30px 100px;
    background-color: #f9f9f9;
}

.reuse-filter__opt--fnd__title {
    position: absolute;
    width: 80px;
    top: 22px;
    left: 50%;
    transform: translateX(-50%);
}

.reuse-filter__opt--fnd__title > div {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #0284a8;
    box-shadow: 0 1px 3px 1px rgba(0,0,0, .75);
    color: #fff;
}

.reuse-filter__opt--cnt {
    position: relative;
    font-size: .8125rem;
    padding-left: 10px;
}

.reuse-filter__opt--cnt::after {
    content: "";
    clear: both;
    display: table;
}

.reuse-filter__opt--cnt__wrapper {
    float: left;
    padding-right: 10px;
    margin-bottom: 20px;
}

.reuse-filter__opt--cnt__wrapper--mid {
    width: 50%;
}

@media (min-width: 800px) {

    .reuse-filter__opt--cnt__wrapper--mid {
        width: 33.333%;
    }
}

@media (min-width: 530px) {

    .reuse-filter__opt--cnt__wrapper {
        padding-right: 30px;
    }
}

.reuse-filter__opt--cnt__title {
    font-weight: 400;
    color: #182427;
    text-align: center;
    font-size: .875rem;
    padding: 3px 0;
    margin: 0;
    border: 1px solid #dcdbdc;
    background-color: #f2f2f2;
}

.reuse-filter__opt--cnt__det {
    list-style: none;
    margin: 0;
    padding: 2px 0;
    border: 1px solid #dcdbdc;
    border-top: none;
}

.reuse-filter__opt--cnt__det--categ {
    height: 84px;
    overflow: hidden;
    overflow-y: auto;
}

.reuse-filter__opt--cnt__det--active {
    background-color: #0284a8;
    color: #fff;
}

.reuse-filter__opt--cnt__det > li {
    float: none;
    padding: 6px 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    position: relative;
}

.reuse-filter__opt--cnt__det > li:last-child {
    border-bottom: none;
}

.reuse-filter__opt--cnt__det > li > span {
    position: absolute;
    right: 10px;
}

@media (min-width: 530px) {

    .reuse-filter__opt--cnt__det > li > span {
        right: 20px;
    }
}

.reuse-filter__opt--cnt__det > li:hover {
    background-color: #0284a8;
    color: #fff;
}

@media (min-width: 530px) {

    .reuse-filter__opt--cnt__det > li {
        padding: 6px 20px;
    }
}

@media (min-width: 530px) {

    .reuse-filter__opt--cnt {
        padding-left: 30px;
    }
}

.reuse-filter__opt--info {
    background-color: #f9f9f9;
    font-size: .8125rem;
}

.reuse-filter__opt--info > h3 {
    text-align: center;
    padding: 5px 0;
    background-color: #dcdbdc;
    font-size: .875rem;
    font-weight: 400;
    margin: 0;
}

.reuse-filter__opt--info__cnt {
    list-style: none;
    margin: 0;
    padding: 14px 10px 0 10px;
}

.reuse-filter__opt--info__cnt::after {
    content: "";
    clear: both;
    display: table;
}

.reuse-filter__opt--info__cnt > li {
    float: left;
    font-size: .8125rem;
    color: #182427;
    background-color: #fff;
    padding: 5px 8px;
    border-radius: 5px;
    margin-right: 14px;
    margin-bottom: 14px;
    font-weight: 400;
    border: 1px solid #dcdbdc;
    position: relative;
}

.reuse-filter__opt--info__cnt > li > div {
    position: absolute;
    top: -9px;
    right: -9px;
    width: 18px;
    height: 18px;
    font-size: .625rem;
    border-radius: 9px;
    border: 1px solid #dcdbdc;
    background-color: #f2f2f2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}

.reuse-filter__opt--btn {
    padding: 5px 10px;
    margin-top: 20px;
    border-top: 1px solid #dcdbdc;
    font-size: .8125rem;
}

.reuse-filter__opt--btn::after {
    content: "";
    clear: both;
    display: table;
}

.reuse-filter__opt--btn > button {
    position: relative;
    padding: 6px 10px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    box-shadow: 0 1px 2px 0 rgba(0,0,0, .85);
}

.reuse-filter__opt--btn > button:disabled {
    opacity: .75;
    cursor: not-allowed;
}

.reuse-filter__opt--btn__cancel {
    background-color: #fff;
    color: #ff1600;
    float: left;
}

.reuse-filter__opt--btn__cancel:hover {
    background-color: #f2f2f2;
}

.reuse-filter__opt--btn__apply {
    background-color: #0284a8;
    color: #fff;
    float: right;
}

.reuse-filter__opt--btn__apply:not(:disabled):hover {
    background-color: #05578b;
}

@media (min-width: 530px) {

    .reuse-filter__opt--btn {
        padding: 5px 30px;
    }
}

@media (min-width: 530px) {

    .reuse-filter__opt {
        padding: 20px;
    }
}

@media (min-width: 800px) {

    .reuse-filter__opt {
        width: 780px;
    }
}   